<template>
	<div class="checksTable dataTable">
		<div class="loading" v-if="isLoading">
			<i class="loading-i fa fa-spinner fa-pulse"></i>
			<span v-if="isNetErr"></span>
			<span v-else></span>
		</div>
		<div v-else>
			<div class="search">
				<div>
					<el-input type="text" v-model="search_val" placeholder="" style="width: 200px;"></el-input>
					<el-button type="primary" @click="search"><i class="fa fa-search"></i></el-button>
					<el-button type="primary" @click="update">{{isUpdate?'...':''}}<i class="fa fa-spinner fa-pulse" v-if="isUpdate"></i></el-button>
				</div>
				<div class="search-right">
					<el-button type="primary" @click="download"></el-button>
				</div>
			</div>
			<el-table
			    :data="tableData"
			    style="width: 100%"
			    :max-height="table_height">
		    <el-table-column
		      type="selection"
		      width="55">
		    </el-table-column>
		    <el-table-column
		      fixed
		      prop="id"
		      label="id"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="check_version_number"
		      label=""
		      >
		    </el-table-column>
		    <el-table-column
		      prop="cells_number"
		      label=""
		      >
		    </el-table-column>
		    <el-table-column
		      prop="class_number"
		      label=""
		      >
		    </el-table-column>
		    <el-table-column
		      prop="classify"
		      label=""
		      :show-overflow-tooltip="true"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="check_date"
		      label="/"
		      :show-overflow-tooltip="true"
		      >
		    </el-table-column>
		    <el-table-column
		      prop="storage_path"
		      label=""
		      :show-overflow-tooltip="true"
		      >
		      <template slot-scope="scope">
				<span class="storage_path" :data-clipboard-text="scope.row.storage_path" @click="copy">{{ scope.row.storage_path }}</span>
				<span></span>
		      </template>
		  </el-table-column>
		      <el-table-column
		      prop="image_format"
		      label=""
		      >
		  </el-table-column>
		      <el-table-column
		      prop="source"
		      label=""
		      :show-overflow-tooltip="true"
		      >
		    </el-table-column>
		    <el-table-column
		      fixed="right"
		      label=""
		      >
		      <template slot-scope="scope">
		        <el-button
		          @click.native.prevent="updateRow(scope.$index, tableData)"
		          type="text"
		          size="small">
		          
		        </el-button>
		      </template>
		    </el-table-column>
		  </el-table>
		</div>
		<div class="pager" v-if="!isLoading">
			<el-pagination
		      @size-change="handleSizeChange"
		      @current-change="handleCurrentChange"
		      :current-page="currentPage"
		      :page-sizes="[20, 50, 100]"
		      :page-size="limit"
		      layout="total, sizes, prev, pager, next, jumper"
		      :total="count">
		    </el-pagination>
		</div>
		<el-dialog title="" :visible.sync="dialogFormVisible" :append-to-body="true" :center="true">		
		  <el-form :model="form">
		  	<el-form-item label="id" :label-width="formLabelWidth">
		      {{ form.id }}
		    </el-form-item>
		    <el-form-item label="" :label-width="formLabelWidth">
		      <el-input v-model="form.source" autocomplete="off"></el-input>
		    </el-form-item>    
		  </el-form>
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="dialogFormVisible = false"> </el-button>
		    <el-button type="primary" @click="submit"> </el-button>
		  </div>
		</el-dialog>
		<el-dialog
		  title=""
		  :visible.sync="DialogVisible2"
		  width="30%"
		  >
		  <a :href="downloadsUrl+'csv'" @click="DialogVisible2 = false">csv</a><br/>
		  <a :href="downloadsUrl+'xlsx'" @click="DialogVisible2 = false">excel</a>
		</el-dialog>
	</div>
</template>

<script>
	import Clipboard from 'clipboard';	//
	const labelList = require('../../api/labelDataList.js');
	var loading;

	export default({
		name: 'checksTable',
		data() {
			return {
				tableData: [],
				isLoading: true,
				isNetErr: false,
				form: {},
				dialogFormVisible: false,
				formLabelWidth: '150px',
				count: 0,
				currentPage: 1,
				offset: 0,
				limit: 20,
				search_val: '',
				labelList: [],
				downloadsUrl: '',
				DialogVisible2: false,
				uploadUrl: '',
				isUpdate: false,
			}
		},
		computed: {
			table_height: function(){
				return this.$store.state.table_height;
			},
			repeated_val: function(){
				return this.$store.state.repeated_val;
			}			
		},
		created: function(){
			this.labelList = labelList;
			this.downloadsUrl = this.API+'/api/v1/checks/details/downloads/?type=';
			this.uploadUrl = this.API + '/api/v1/checks/uploads/';
		},
		mounted: function(){
			var that = this;
			if(that.$store.state.repeated_val != ''){
				that.search_val = that.$store.state.repeated_val;
				that.search();
			}
			else
				that.getChecks();
		},
		destroyed: function(){
			this.$store.commit('setRepeated_val','');
		},
		methods: {
			// 
			getChecks: function(url='/api/v1/checks/?limit=20&offset=0'){
				var that = this;
				that.$axios.get(this.API+url).then(res => {
					that.tableData = res.data.results;
					that.isLoading = false;
					that.count = res.data.count;
				}).catch(err => {
					that.isNetErr = true;
				});
			},
			// 
			deleteRow: function(index, rows){
				this.$confirm(', ?', '', {
		          confirmButtonText: '',
		          cancelButtonText: '',
		          type: 'warning'
		        }).then(() => {
		          this.$axios.delete(this.API+'/api/v1/checks/'+rows[index].id+'/').then(res => {
					this.$message({
		            	type: 'success',
		            	message: '!'
		          	});
		          }).catch(err => {
		          	this.$message('')
		          });
		          
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: ''
		          });          
		        });
			},
      		// 
      		updateRow: function(index, rows){
      			this.form = JSON.parse(JSON.stringify(rows[index]));
      			this.dialogFormVisible = true;
      		},
      		// 
      		submit: function(){
      			this.$axios.patch(this.API+'/api/v1/checks/'+this.form.id+'/',this.form).then(res => {
		    		this.$message({
		    			message: '',
		    			type: 'success'
		    		});
		    		this.dialogFormVisible = false;
		    		if(this.search_val != '')
		    			this.search();
		    		else
		    			this.getChecks('/api/v1/checks/?limit='+this.limit+'&offset='+(this.currentPage-1)*this.limit);
		    		
		    	}).catch(err => {
		    		
		    	});
      		},
      		// 
      		handleSizeChange(val) {
		        this.limit = val;
		        this.handleCurrentChange(this.currentPage);
		    },
		    // 
		    handleCurrentChange(val) {
		    	this.isLoading = true;
		    	this.currentPage = val;
		        var url = '/api/v1/checks/?limit='+this.limit+'&offset='+(val-1)*this.limit;
		        this.getChecks(url);
		    },
		    // 
		    search: function(){
		    	this.isLoading = true;
		    	if(this.search_val == '')
		    		this.getChecks();
		    	else
		    		this.getChecks('/api/v1/checks/?id='+this.search_val);
		    },	    
		    // 
		    download: function(){
		    	var types = 'csv';
		    	this.DialogVisible2 = true;
		    	
		    },
		    // 
		    copy: function(val){
		    	const copy = new Clipboard('.storage_path');
		    	this.$message({
		        	message: '',
		        	type: 'success'
		        })
		    },
		    // 
		    update: function(){
		    	if(this.isUpdate) return;
		    	var update_type = '';
		    	this.$confirm('','',{
		    		confirmButtonText: '',
		    		cancelButtonText: '',
		    		type: 'warning'
		    	}).then(() => {
			    	this.$confirm('','',{
			    		confirmButtonText: '',
			    		cancelButtonText: '',
			    		type: 'warning'
			    	}).then(() => {
			    		update_type = 'sample';
			    		this.isUpdate = true;
			    		this.$axios.post(this.API+'/api/v1/checks/updates/',{update_type: update_type}).then(res => {
				          	this.isUpdate = false;
				          	this.$notify({
					          title: '',
					          message: res.data.msg+'('+res.data.cost_time+'',
					          duration: 0
					        });
				          }).catch(err => {
				          	this.$notify({
				          		title: '',
				          		message: '',
				          		duration: 0
				          	});
				          });			          
			        }).catch(() => {
			          update_type = 'detail'; 
			          this.isUpdate = true; 
			          this.$axios.post(this.API+'/api/v1/checks/updates/',{update_type: update_type}).then(res => {
			          	this.isUpdate = false;
			          	this.$notify({
				          title: '',
				          message: res.data.msg+'('+res.data.cost_time+'',
				          duration: 0
				        });
			          }).catch(err => {
			          	this.$notify({
			          		title: '',
			          		message: '',
			          		duration: 0
			          	});
			          });       
			        });
		    				          
		        }).catch(() => {
		          this.$message({
		            type: 'info',
		            message: ''
		          });          
		        });
		    }

    	},

	})

</script>

<style scoped="scoped">
	.loading{display: flex;justify-content: center;align-items: center;height: 100%;flex-direction: column;line-height: 50px;}
	.loading-i{font-size: 35px;color:#367dd5;}
	.storage_path{cursor: pointer;}
	.search-right{display: flex;}
</style>


